*{
  box-sizing: border-box  ;
  font-family: sans-serif;
}
.App{
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr ;
  gap: 10px;
  width: 1170px;
  margin: 0 50px;
  padding: 0 15px ;
}
.app-content{
  border: 1px solid blue;
  grid-row: 2/3;
  padding: 20px;
  min-height: 800px;
}

