.selectedPage{
  font-weight: 700;
}
.pagination{
  margin: 20px auto;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
}
.pagination ul{
  margin: 0;
  padding-left: 0;
}
.pagination li{
  text-align: center;
  width: 30px;
  height: 20px;
  border: 1px solid black;
  list-style: none;
  margin: 0 10px;
  display: inline-block;
  cursor: pointer;
}
.button{
  outline: none;
  cursor: pointer;
  border: none;
  border-bottom: 3px solid gray;
}
.button:active{
  border-bottom: 2px solid gray;
}