.header{
  display: flex;
  justify-content: space-between;
  padding:  0 40px;
  align-items: center;
  border: 1px solid green;
  grid-column: 1/3;
}
.logo{
  background: blue;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: relative;
}
.logo:before{
  content: '';
  position: absolute;
  display: block;
  border-radius: 50%;
  border: 2px solid white;
  width: 30px;
  height: 30px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

}
.loginBlock a{
  text-decoration: none;
  color: black;
  font-weight: 700;
}
.login{
  font-weight: 700;
}
