.usersWrap{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.user{
  width: 430px;
  position: relative;
  height: 100px;
  margin: 10px 0;
  border-radius: 20px;
  padding: 20px 20px 20px 100px;
  border: 1px solid black;
}
.userLogo{
  left: 20px;
  top: 20px;
  position: absolute;
  display: inline-block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.name{
  margin: 0;
}
.status{
  margin: 0;
  color: gray;
  font-size: 12px;
}
.follow, .unfollow{
  outline: none;
  cursor: pointer;
  position: absolute;
  right: 20px;
  bottom: 20px;
  border-radius: 20px;
  border: none;
  border-bottom: 3px solid #336832;
  padding: 5px 10px;
  background: #3c803b;
  color: white;
}
.follow:active{
  border-bottom: 2px solid #336832;
}
.unfollow{
  background: #804e2b;
  color: white;
  border-bottom: 3px solid #5B3423;
}
.unfollow:active{
  border-bottom: 2px solid #5B3423;
}
