@keyframes ldsDualRing {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes ldsDualRing {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.ldsDualRing {
  position: relative;
}

.ldsDualRing div {
  box-sizing: border-box;
}
.ldsCss{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ldsDualRing > div {
  position: absolute;
  width: 168px;
  height: 168px;
  top: 16px;
  left: 16px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #000000 transparent #000000 transparent;
  -webkit-animation: ldsDualRing 1s linear infinite;
  animation: ldsDualRing 1s linear infinite;
}

.ldsDualRing > div:nth-child(2) {
  border-color: transparent;
}

.ldsDualRing > div:nth-child(2) div {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.ldsDualRing > div:nth-child(2) div:before,
.ldsDualRing > div:nth-child(2) div:after {
  content: "";
  display: block;
  position: absolute;
  width: 8px;
  height: 8px;
  top: -8px;
  left: 72px;
  background: #000000;
  border-radius: 50%;
  box-shadow: 0 160px 0 0 #000000;
}

.ldsDualRing > div:nth-child(2) div:after {
  left: -8px;
  top: 72px;
  box-shadow: 160px 0 0 0 #000000;
}

.ldsDualRing {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}
.preloader{
  width: 100%;
  height: 100%;
}